.header__top{
  background-color: #fff8ef;
  padding: 0 0 16px 0;
  position: sticky;
  top: 0;
  z-index: 2;
  box-shadow: 0 4px 2px -2px #e3d2d0ab;
}
.header__top-wrap{
  text-align: right;
  color: #824c45;  
}
.burger_menu{
  display: none;
  float: left;
  margin-left: 10px;
  background-image: url(../../assets/images/menu1.png);
  height: 20px;
  width: 20px;
  border: none;
  background-color: inherit;
}
.header__commercial {
  display: inline-block;
  margin-right: 30%;
  font-weight: 700;
  font-size: 17px;
  font-family: "MPR";
}
.header__list{
  list-style: none;
  display: inline-block;
}
.header__list li{
  margin-right: 20px;
  display: inline-block;
  position: relative;
}
.header__heart{
  width: 22px;
  position: relative;
  top: 3px;
}
.header__cart{
  width: 17px;
  position: relative;
  top: 0px;
}
.header__cart-counter{
  position: absolute;
  top: 13px;
  left: -5px;
  font-size: 11px;
  background-color: #48231c;
  color: white;
  width: 15px;
  height: 15px;
  text-align: center;
  padding-top: 1px;
  border-radius: 20px;
  z-index: 1;
}
/* .header__cart::before{
  width: 17px;
} */
.header__user{
  width: 17px;
  position: relative;
  top: 1px;
  cursor: pointer;
}
#lang{
  border: none;
  background-color: inherit;
  width: 45px;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
  color: #48231c;
  font-size: 20px;
  position: relative;
  top: -2px;
  cursor: pointer;
}
.logo{
  display: inline-block;
  margin-left: 90px;
}
.header__bottom{
  margin-top: 50px;
}
.header__bottom-text{
  display: inline-block;
  text-align: end;
  margin-left: 18%;
  position: relative;
  top: -50px;
  color: #824c45;
  font-family: 'Acumni';
  /* z-index: 1; ?????????????????????????*/
}
.header__bottom-text__name{
  font-size: 30px;
}
.header__user-button{
  border: none;
  background-color: inherit;
}
.popup-content {
  margin: auto;
  background: rgb(253, 253, 253);
  padding: 10px 15px 15px 15px;
  /* top: -286px;
  left: 499px; */
  box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 3px;
}
.popup-arrow {
  -webkit-filter:drop-shadow(0 -3px 3px rgba(0,0,0,.16));
}
[role='tooltip'].popup-arrow {
  margin-right: 10px;
}
[role='tooltip'].popup-content {
  width: 230px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 3px;
}
[data-popup='tooltip'].popup-overlay {
  background: rgba(128, 128, 128, 0.274);
}

@keyframes anvil {
  0% {
    transform: scale(1) translateY(0px);
    opacity: 0;
    box-shadow: 0 0 0 rgba(241, 241, 241, 0);
  }
  1% {
    transform: scale(0.96) translateY(10px);
    opacity: 0;
    box-shadow: 0 0 0 rgba(241, 241, 241, 0);
  }
  100% {
    transform: scale(1) translateY(0px);
    opacity: 1;
    box-shadow: 0 0 500px rgba(241, 241, 241, 0);
  }
}
.popup-content {
  -webkit-animation: anvil 0.3s cubic-bezier(0.38, 0.1, 0.36, 0.9) forwards;
}

.buttonchick{
  font-family: acumni;
}
.email,
.password{
  padding: 6px 0px 6px 10px;
  border: unset;
  border: 1px solid;
  border-color: grey;
  margin-bottom: 15px;
  width: 200px;
}
.email:focus,
.password:focus{
  outline: none;
}
.Enter__button{
  width: 200px;
  padding: 6px 0px 6px 0px;
  border: none;
  background-color: #824c45;
  color: white;
  font-family: inherit;
  font-size: 16px;
  cursor: pointer;
}
.login__registration{
  border-bottom: 1px solid rgba(128, 128, 128, 0.363);
}
.registration,
.login{
  font-family: inherit;
  background-color: inherit;
  border: none;
  font-size: 16px;
  text-decoration: underline;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 15px;
}
.login{
  margin-left: 34px;
}
.email_label,
.password_label{
  color: #824c45;
  font-size: 13px;
  margin-top: 10px;
}
.google,
.facebook{
  background-color: inherit;
  border: unset;
  border: 1px solid;
  border-color: rgba(128, 128, 128, 0.253);
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 30px;
  padding-right: 10px;
  margin-top: 10px;
  font-weight: 700;
  cursor: pointer;
}
.facebook{
  margin-left: 10px;
}
.google::before{
  content: url(../../assets/images/search.png);
  margin-right: 5px;
  position: absolute;
  left: 25px;
}
.facebook::before{
  content: url(../../assets/images/facebook.png);
  margin-right: 5px;
  position: absolute;
  left: 121px;
}
