.cart__label{
    background-color: #fff8ef;/*#8080802b;*/
    font-family: 'MPR';
    font-size: 30px;
    padding: 10px 0 10px 10%;
    box-shadow: 0 4px 2px -2px #00000014;
    color: #824c45;
    margin-top: 54px;
}
.cart__label-counter{
    display: inline-block;
    font-size: 16px;
    margin-left: 10px;
    position: relative;
    top: 4px;
}
.cart{
    max-width: 1035px;
    margin: auto;
    margin-top: 30px;
    text-align: center;
}
.display__items{
    display: inline-block;
    /* margin-left: 10px;
    margin-right: 10px; */
    max-width: 650px;
    /* margin: auto; */
    width: 100%;
}
.cart__item{
    padding-bottom: 15px;
    border-bottom: 2px solid #00000021;
    margin-top: 30px;
    margin-left: 10px;
    margin-right: 10px;
}









/* .item-enter {
    opacity: 0;
  }
  .item-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  } */
  .cart__item-animation-exit {
    opacity: 1;
  }
  .cart__item-animation-exit-active {
    /* transform: translate(-100%); */
    transform: translate(-350px);
    transition: all 500ms ease-in;
  }








.cart__item-photo{
    width: 120px;
    /* background-color: #00000017;; */
    height: 120px;
    display: inline-block;
    vertical-align: top;
    margin: auto;
}
.cart__item-description{
    display: inline-block;
    margin-left: 60px;
    font-family: 'MPR';
    height: 120px;
    position: relative;
    text-align: start;
}
.cart__item-buttons{
    display: inline-block;
    vertical-align: top;
    margin-left: 80px;
}
.cart__product-name{
    /* font-family: 'MPR'; */
    color: #824c45;
    font-size: 17px;
    max-width: 200px;
    min-width: 200px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.cart__product-description{
    margin-top: 10px;
}
.cart__product-remove{
    border: none;
    border-bottom: 1px solid;
    color: #a97670c7;;
    background-color: inherit;
    position: absolute;
    bottom: 0;
    cursor: pointer;
    font-weight: 550;
    margin-bottom: 15px;
}
.cart__item-qty{
    display: inline-block;
    vertical-align: top;
}
.cart__item-price{
    display: inline-block;
    vertical-align: top;
    color: #824c45bf;
    margin-left: 47px;
    font-family: 'MPR';
    font-weight: 600;
}
.cart__summary{
    display: inline-block;
    vertical-align: top;
    min-width: 300px;
    font-family: 'MPR';
    /* margin-left: 10px;
    margin-right: 10px; */
    padding-left: 25px;
    border-left: 2px solid #00000012;
    max-width: 650px;
    margin: 30px auto 0 auto;;
}
.summary{
    color: #64352f;
    font-weight: 600;
    letter-spacing: 2px;
    font-size: 17px;
}
.taxes,
.total{
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    letter-spacing: 2px;
}
.taxes{
    color: #824c45;
    margin-top: 20px;
}
.total{
    color: #824c45;
    font-weight: 600;
    margin-top: 7px;
}
.checkout{
    border: none;
    background: #bf321f;/*#824c45;*/
    width: 100%;
    padding: 10px;
    font-family: 'MPR';
    color: white;
    font-weight: 600;
    letter-spacing: 2px;
    margin-top: 10px;
    cursor: pointer;
    border-radius: 4px;
}
.cart__comercial{
    font-family: serif;
    text-align: center;
    color: #824c45;
    margin: 20px 0 0 0;
    border-bottom: 2px solid #00000021;
    padding-bottom: 25px;
    font-size: 17px;
}