/* Product net (ShopPage.js) */
.wrapper{
  display: flex;
  justify-content: space-between;
}
.products{
  text-align: center;
}
.shop__main-block{
  background-color: #fff8ef;
  margin-top: 40px;
  padding-top: 25px;
  padding-bottom: 30px;
}
.product__net{
  max-width: 1000px;
  margin: auto;
}
.product__net-description{
  text-align: center;
  font-size: 20px;
  color: #824c45;
  margin-top: 20px;
  font-weight: bold;
  font-family: 'Acumni';
  padding-bottom: 30px;
}
.product{
  background-color: #ffffff;
  max-width: 235px;
  width: 100%;
  display: inline-block;
  margin: 14px 7px 0 7px;
  border-radius: 2%;
  box-shadow: 0px 0px 9px #c3c1c1;
  text-align: -webkit-auto;
  /* padding-top: 10px; */
  position: relative;
  animation: 800ms show ;
}

@keyframes show {
  from { opacity: 0; transform: scale(0.9)}
  to { opacity: 1; transform: scale(1)}
}
.product__disabled{
  position: absolute;
  /* width: 100%;
  height: 100%; */
  padding-top: 7px;
  color: #bf321f;
  padding-left: 12px;
  font-family: 'MPR';
  font-size: 18px;
  z-index: 1;
}
.new{

}
.new::after{
  background-image: url(../../assets/images/new.png);
  position: absolute;
  top: 0;
  right: 0;
  content: "";
  width: 64px;
  height: 64px;
  background-size: 64px 64px;
}
.product__image{
  display: block;
  margin: auto;
  border-radius: 3%;
  width: 200px;
  height: 200px;
}
.product__name{
  color: #66382dc7;
  font-size: 17px;
  overflow: hidden;
  font-weight: 600;
  font-family: 'MPR';
  text-align: center;
}
.product__name-card{
  /* margin-left: 18px; */
  margin-top: 8px;
  /* max-width: 200px; */
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 6px;
  min-height: 67px;
  max-height: 67px;
}
.product__price,
.product__price-sale{
  color: #824c45;
  font-weight: 900;
  font-size: 23px;
  font-family: 'MPR';
}
.product__price-sale{
  text-decoration-line: line-through;
  position: absolute;
  color: #da4936;
  bottom: 75px;
  left: 53px;
}
/* display: none;
&lt;
- Crashes Chrome on hover */
input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
  /* &lt;
  -- Apparently some margin are still there even though it&#39;
  s hidden */
}
.ptoduct__qty-holder {
  display: flex;
  max-height: 30px;
}
.ptoduct__qty-holder input {
  outline: none;
  width: 30px;
  text-align: center;
  border: 2px solid #a97670c7;
  -webkit-appearance: none;
  -moz-appearance: textfield;
  font-family: 'MPR';
  color: #a97670c7;
  font-weight: bold;
  font-size: 18px;
}
.ptoduct__qty-holder button {
  padding: 1px 7px;
  font-size: 20px;
  font-weight: bold;
  background-color:#a97670c7; 
  border: none;
  color: #ffffff;
}
.ptoduct__qty-dec{
  border-radius: 3px 0 0 3px;
  cursor: pointer;
}
.ptoduct__qty-inc{
  border-radius: 0 3px 3px 0;
  cursor: pointer;
}
.product__card-button,
.product__card-buttons{
  padding-top: 16px;
  padding-bottom: 20px;
  display: flex;
  justify-content: space-around;
}
.product__card-buttons{
  margin-left: 54px;
}
.product__add {
  /* background-color: #a97670c7; */
  background-color: #bf321f;
  border: none;
  border-radius: 4px;
  font-family: 'MPR';
  padding-left: 28px;
  padding-top: 5px;
  padding-bottom: 5px;
  color: white;
  font-size: 15px;
  padding-right: 6px;
  position: relative;
  cursor: pointer;
  min-width: 104px;
}
.product__add:hover{
  background-color: #66382dc7;
}
.product__add:active{
  transform: scale(.9);
}
.product__add::before{
  background-image: url(../../assets/images/hope.png);
  position: absolute;
  margin-left: -21px;
  content: "";
  width: 18px;
  height: 18px;
  background-size: 18px 18px;
}
.product__add-favourite{
  border: none;
  background-color: inherit;
  position: relative;
  cursor: pointer;
}

.product__add-favourite:hover{
  transform: scale(1.1);
}

.product__remove-button{
  background-image: url(../../assets/images/x-button.png);
  background-repeat: no-repeat;
  width: 20px;
  height: 20px;
  background-color: inherit;
  border: none;
  cursor: pointer;
  position: relative;
  right: -209px;
  top: 8px;
}
.product__bottom-info{
  display: flex;
  justify-content: space-between;
  max-width: 200px;
  margin-left: 17px;
  margin-top: 9px;
}




