.filter__main,
.filter__sidebar{
    margin-top: 76px;
    /* margin-top: 57px; */
    max-width: 240px;
    min-width: 240px;
    /* padding: 14px 20px 0px 36px; */
    padding: 14px 41px 0px 27px;
}
.filter__sidebar{
    text-align: left;
}
.filter__all{
    margin-bottom: 10px;
    font-family: 'MPR';
    font-size: 20px;
    color: #824c45;
    letter-spacing: 3px;
    border-bottom: 1px solid #824c4552;;
    padding-bottom: 7px;
}
.filter__all button{
    background-color: inherit;
    border: none;
    text-decoration: underline;
    color: #824c45;
    font-size: 18px;
    font-family: 'MPR';
    cursor: pointer;
}
.filter__checkbox {
    position: relative;
    padding-left: 24px;
    cursor: pointer;
}
input[type=checkbox] {
    visibility: hidden;
}
.filter__mark {
    position: absolute;
    top: 7px;
    left: 0;
    height: 13px;
    width: 13px;
    border: 1px solid #824c45;
    border-radius: 15%;
} 
.filter__mark:after {
    content: "";
    position: absolute;
    display: none;
}
.filter__checkbox input:checked ~ .filter__mark:after {
    display: block;
}
.filter__checkbox .filter__mark:after {
    left: 3px;
    bottom: 2px;
    width: 5px;
    height: 8px;
    border: solid #009688;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}