/* Product (ProductPage.js) */
.product__agitation{
  text-align: center;
  background-color: #fff8ef;
  padding: 3px 0 3px 0;
  font-family: "MPR";
  color: #824c45;
  margin-top: 50px;
}
.product__slides{
  max-width: 230px;
  min-width: 230px;
  margin-bottom: 30px;
  display: inline-block;
  vertical-align: top;
}
.product__details{
  display: inline-block;
  position: relative;
  margin-left: 270px;
  max-width: 305px;
  text-align: start;
}
.slider__image{
  text-align: center;
}
.slick-next:before, 
.slick-prev:before {
  color: #824c45;
}
.product__buttons{
  display: flex;
  justify-content: space-between;
  max-width: 305px;
  min-width: 305px;
}
.product__description{
  margin-top: 20px;
  font-family: 'MPR';
  color: #c7b0b3;
}
.product__description-full{
  margin: 20px 0 20px 0;
  font-family: 'MPR';
  color: #66382dc7;
}
.product__info{
  text-align: center;
  margin-top: 40PX;
}
.product__info-wrap{
  background-color: #fff8ef;
  padding: 1px 0 40px 0;
  margin-top: 40px;
}
.product__commercial{
  text-align: center;
  padding: 3px 0 3px 0;
  font-family: "MPR";
  color: #824c45;
  max-width: 540px;
  margin: 0 auto 0 auto;
  padding-top: 30px;
  margin-bottom: 20px;
}
.commercial__slides{
  max-width: 1200px;
  margin:0 auto 0 auto;
  margin-bottom: 100px;
}
.favoutites{
  color: #824c45;
  font-family: "MPR";
  font-size: 20px;
  padding-top: 20px;
  margin-left: 90px;
}
.favoutites__block{
  background-color: #fff8ef;
  margin-top: 40px;
  padding-bottom: 30px;
}
.favourites__info{
  text-align: center;
  color: #824c45;
  font-family: "MPR";
  margin-top: 50px;
  margin-bottom: 100px;
}
.Login__link{
  border: none;
  background-color: inherit;
  color: brown;
  margin-top: 10px;
  cursor: pointer;
  text-decoration: underline;
  font-family: 'MPR';
  font-size: 17px;
}
